/* Slick Slider Arrows */
ul.slick-dots {
  flex-wrap: wrap;
}

.slick-arrow {
  width: 40px !important;
  height: 40px !important;
  font-size: 0 !important;
  background: rgba(0, 0, 0, 0) !important;
  cursor: pointer !important;
  top: 40%;
  transform: translateY(-50%);
  border: none !important;
  position: absolute;
  display: block;
}

.slick-arrow:before {
  width: 16px;
  height: 16px;
  content: "";
  display: block;
  position: absolute;
  top: 11px;
}

.slick-prev {
  z-index: 1;
}

.slick-prev:before {
  border-top: 3px solid #666;
  border-left: 3px solid #666;
  transform: rotate(-45deg);
}

.slick-prev[aria-disabled=true] {
  opacity: 0.4;
  pointer-events: none;
}

.slick-next {
  right: 20px;
}

.slick-next:before {
  border-top: 3px solid #666;
  border-left: 3px solid #666;
  transform: rotate(135deg);
}

.slick-next[aria-disabled=true] {
  opacity: 0.4;
  pointer-events: none;
}

/* Slick Slider Dots */
.slick-dots {
  text-align: center; /* Center align dots */
  margin-top: 20px; /* Space above dots */
}

.slick-dots li {
  display: inline-block;
  margin: 0 5px; /* Space between dots */
}

.slick-dots li button {
  font-size: 0; /* Hide default button text */
  background-color: #ccc; /* Dot color */
  width: 12px; /* Dot width */
  height: 12px; /* Dot height */
  border-radius: 50%; /* Rounded dots */
  padding: 5px;
}

.slick-dots li.slick-active button {
  background-color: #333; /* Active dot color */
}

.slick-dots li button:hover {
  background-color: #555; /* Dot color on hover */
}

